import { Component, OnInit, ViewChild } from '@angular/core';
import { EmpresasService } from 'src/app/services/empresas/empresas.service';
import { ToastrService } from 'src/app/common/toastr.service';
import { MatTableDataSource } from '@angular/material/table';
import { MatSort } from '@angular/material/sort';
import { SolicitudEmpresaService } from './solicitud-empresa.service';
import { Router } from '@angular/router';
import { ListSolicitudEmpresa, ResponseSolicitudEmpresa, SolicitudEmpresa } from '../../../utils/models/solicitud-empresa';

import swl from 'sweetAlert';

export interface PeriodicElement {
  tipoVinvulacion: string;
  perfil: string;
  perdonaEncargada: string;
  telefono: string;
  cargoDes: string;
  fechaInicio: string;
}
@Component({
  selector: 'app-solicitud-empresa',
  templateUrl: './solicitud-empresa.component.html',
  styleUrls: ['./solicitud-empresa.component.scss']
})
export class SolicitudEmpresaComponent implements OnInit {
  empresas: any[];
  solicitudes: SolicitudEmpresa[]

  displayedColumns: string[] = ['perfil', 'tipoVinvulacion', 'perdonaEncargada', 'telefono', 'cargoDes', 'fechaInicio', 'accion'];
  dataSource = new MatTableDataSource();
  nit = '';
  nombre = '';
  idEmpresa = 0;
  title = '';
  mensaje = '';
  ruta = '';
  notificacion = false;
  solicitar = false;
  tabla = false;

  @ViewChild(MatSort, { static: true }) sort: MatSort;

  constructor(private solicitudEmpresaService: SolicitudEmpresaService, private toastr: ToastrService, private router: Router) {
    this.dataSource = new MatTableDataSource(this.solicitudes);
  }

  ngOnInit() {
    if (sessionStorage.getItem('datos-empresa')) {
      const empresa = JSON.parse(sessionStorage.getItem('datos-empresa'));
      if (sessionStorage.getItem('solicitud-practicante')) {
        this.solicitudes = JSON.parse(sessionStorage.getItem('solicitud-practicante'));
        this.tabla = true;
      }
      this.idEmpresa = empresa.idEmpresa;
      this.nombre = empresa.nombreEmpresa;
    }
    if (sessionStorage.getItem('nit')) {
      this.nit = sessionStorage.getItem('nit');
    }
    this.dataSource = new MatTableDataSource(this.solicitudes);
    this.dataSource.sort = this.sort;
  }

  ValidarExistencia() {
    sessionStorage.removeItem('datos-empresa');
    sessionStorage.removeItem('solicitud-practicante');
    this.solicitudEmpresaService.getValidarExisteEmpresa(this.nit).subscribe(
      res => {
        this.idEmpresa = res.idEmpresa;
        this.nombre = res.nombreEmpresa;
        this.buscarEmpresa();


      sessionStorage.setItem('nit', this.nit);
        sessionStorage.setItem('datos-empresa', JSON.stringify(res));
      },
      error => {
        swl({
          title: "",
          text: 'El sistema presenta inconvenientes',
          icon: "warning",
          buttons: [false, true],
        }).then((willDelete) => {
          this.router.navigate(['/inicio'])
        });
      }
    );
  }

  buscarEmpresa() {
    sessionStorage.removeItem('datos-empresa');
    sessionStorage.removeItem('solicitud-practicante');
    if (this.idEmpresa === 0) {
      this.ruta = "registro-de-empresa";
      swl({
        title: "La empresa no existe.",
        text: "Desea crearla?",
        buttons: ["No", "Si"],

      })
        .then((willDelete) => {
          if (willDelete) {
            this.router.navigate(["registro-de-empresa"]);
          } else {
          }
        });
    }
  }

  EnviarSolicitud() {
    let body: ResponseSolicitudEmpresa = {
      listaSolicitudes: this.solicitudes
    }
    this.solicitudEmpresaService.postEnviarSolicitudEmpresa(body).subscribe(
      res => {
        swl(res.mensaje,`Solicitud # : ${JSON.stringify(res.numerosSolicitud.join(','))}`, "success",
          {
            buttons: [false, "OK"],
          }).then((willDelete) => {
            this.nit = '';
            this.router.navigate(["/inicio"]);
            sessionStorage.removeItem('datos-empresa');
            sessionStorage.removeItem('solicitud-practicante');
          });
      },
      error => {
        swl({
          title: "",
          text: 'El sistema presenta inconvenientes',
          icon: "warning",
          buttons: [false, true],
        }).then((willDelete) => {
          this.router.navigate(['/inicio'])
        });
      });

  }

  eliminarSolicitud(data: SolicitudEmpresa){
    swl({
      title: "Desea eliminar esta solicitud de practicante?",
      buttons: ["No", "Si"],

    })
      .then((willDelete) => {
        if (willDelete) {
          this.tabla = false;
          for (let index = 0; index < this.solicitudes.length; index++) {
            const element = this.solicitudes[index];
            if(element.idSolicitud === data.idSolicitud){
              this.solicitudes.splice(0, data.idSolicitud);
              sessionStorage.setItem('solicitud-practicante', JSON.stringify(this.solicitudes));
              this.dataSource = new MatTableDataSource(this.solicitudes);
              if(this.solicitudes.length == 0){
                this.tabla = false;
              } else {
                this.tabla = true;
              }
            }
          }
          this.solicitudes = this.solicitudes;
        } else {
        }
      });
  }

  editarSolicitud(dataEdit) {
    sessionStorage.setItem('SolicitudEditar', JSON.stringify(dataEdit));
    this.router.navigate(['solicitar-practicante']);
  }

  cancelar() {
    this.nit = '';
    this.router.navigate(['/inicio']);
    sessionStorage.removeItem('datos-empresa');
    sessionStorage.removeItem('solicitud-practicante');
  }
}
