import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { ResponseFacultades } from 'src/utils/models/facultades';
import { environment, headersRequest } from 'src/environments/environment';
import { Observable } from 'rxjs';
import { apis, endpointsMaestros } from 'src/utils/const';
import { ResponseProgramasPorFacultad } from 'src/utils/models/programas-por-facultad';

@Injectable({
  providedIn: 'root'
})
export class RegistroEstudianteService {

  constructor(private http: HttpClient) { }

  getFacultades(): Observable<ResponseFacultades> {
    const headers =  new HttpHeaders()
    .set('SCOrigen', headersRequest.SCOrigen)

    return this.http.get<ResponseFacultades>(
      environment.endpoint +
      apis.maestros +
      endpointsMaestros.getObtenerFacultades, {headers}
    );
  }

  getProgramaPorFacultad(idFacultad): Observable<ResponseProgramasPorFacultad> {
    const params =  new HttpParams()
    .set('idFacultad', idFacultad)

    return this.http.get<ResponseProgramasPorFacultad>(
      environment.endpoint +
      apis.maestros +
      endpointsMaestros.getObtenerProgramasPorFacultad, {params}
    );
  }
}
