import { Injectable } from '@angular/core';
import { HttpClient, HttpParams} from '@angular/common/http';
import { Observable } from 'rxjs';
import { ResponseValidarRequisitosEstudiante } from 'src/utils/models/validar-requisitos-estudiante';
import { environment } from 'src/environments/environment';
import { apis, endpointsEstudiante, endpointsMaestros } from 'src/utils/const';
import { Estudiante, ResponseCreaEstudiante } from 'src/utils/models/crear-estudiante';
import { ResponseMunicipios } from 'src/utils/models/municipios';
import { ResponseSubirArchivo } from '../../../utils/models/subir-archivo';
import { endpointsGeneral } from '../../../utils/const';

@Injectable({
  providedIn: 'root'
})
export class FormularioEstudianteService {

  constructor( private http: HttpClient) { }

  getValidarRequisitosEstudiante(identificacion): Observable<ResponseValidarRequisitosEstudiante> {
    const params =  new HttpParams()
    .set('identificacion', identificacion)

    return this.http.get<ResponseValidarRequisitosEstudiante>(
      environment.endpoint +
      apis.estudiante +
      endpointsEstudiante.getValidarRequisitosEstudiante, {params}
    );
  }

  getObtenerMunicipios(): Observable<ResponseMunicipios> {

    return this.http.get<ResponseMunicipios>(
      environment.endpoint +
      apis.maestros +
      endpointsMaestros.getObtenerMunicipios
    );
  }

  postCrearEstudiante(body: Estudiante) {

    return this.http.post<ResponseCreaEstudiante>(
      environment.endpoint +
      apis.estudiante +
      endpointsEstudiante.postCrearEstudiante, body
    );
  }

  postSubirArchivo(formData: FormData) {
    return this.http.post<ResponseSubirArchivo>(
      environment.endpoint +
      apis.general +
      endpointsGeneral.postSubirArchivo, formData
    );
  }
}
