import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MenuItem } from '../../utils/models/menuItem';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-inicio',
  templateUrl: './inicio.component.html',
  styleUrls: ['./inicio.component.scss']
})
export class InicioComponent implements OnInit {

  itemsMenu : Array<MenuItem>;
  textoinicio : string = "Bienvenido al Sistema de gestin de prcticas profesionales del Instituto Tecnolgico Metropolitano. Selecciona el mdulo correspondiente para comenzar con el proceso."
  constructor(private router: Router) { }

  ngOnInit() {
    this.itemsMenu = [
       {imagen : '/assets/images/student.png', titulo: 'Estudiantes', descripcion : 'Si cumples con los requisitos para empezar tu prctica profesional, regstrate aqu!', pagina : '/registro-de-estudiante'},
       {imagen : '/assets/images/company.png', titulo: 'Empresas', descripcion : 'Si ests buscando un practicante, realiza tu solicitud aqu!', pagina : '/solicitud-empresa'},
    ]
    sessionStorage.removeItem('datos-empresa')
    sessionStorage.removeItem('nit')
  }

  goToPage(pagename:string)
  {
    this.router.navigate([pagename]);
  }
}
