import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { ResponseMunicipios } from 'src/utils/models/municipios';
import { environment } from 'src/environments/environment.prod';
import { apis, endpointsMaestros } from 'src/utils/const';
import { ResponseSectoresEconomicos } from '../../../utils/models/sectores-economicos';
import { Empresa, ResponseCrearEmpresa } from '../../../utils/models/crear-empresa';
import { endpointsEmpresa } from '../../../utils/const';

@Injectable({
  providedIn: 'root'
})
export class RegistroEmpresaService {

  constructor(private http: HttpClient) { }

  getObtenerMunicipios(): Observable<ResponseMunicipios> {

    return this.http.get<ResponseMunicipios>(
      environment.endpoint +
      apis.maestros +
      endpointsMaestros.getObtenerMunicipios
    );
  }
  getObtenerSectoresEconomicosEmpresa(): Observable<ResponseSectoresEconomicos> {

    return this.http.get<ResponseSectoresEconomicos>(
      environment.endpoint +
      apis.maestros +
      endpointsMaestros.getObtenerSectoresEconomicosEmpresa
    );
  }

  getCrearEmpresa(body: Empresa){
    return this.http.post<ResponseCrearEmpresa>(
      environment.endpoint +
      apis.empresas +
      endpointsEmpresa.postCrearEmpresa,
      body
    );
  }
}
