import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { RegistroEmpresaService } from './registro-empresa.service';
import { Router } from '@angular/router';
import { Empresa } from '../../../utils/models/crear-empresa';
import { ResponseValidarExisteEmpresa } from '../../../utils/models/validar-existe-empresa';
import swl from 'sweetAlert';

@Component({
  selector: 'app-registro-empresa',
  templateUrl: './registro-empresa.component.html',
  styleUrls: ['./registro-empresa.component.scss']
})
export class RegistroEmpresaComponent implements OnInit {
  registroForm: FormGroup;
  municipios;
  sectoresEconomicos;
  cargando = false;
  constructor(private fb: FormBuilder, private registroEmpresaService: RegistroEmpresaService, private router: Router) { }

  ngOnInit() {
    this.initForm();
    this.obtenerRecursos();
    if (sessionStorage.getItem('nit')) {
      this.registroForm.get('nit').setValue(sessionStorage.getItem('nit'));
    }
  }

  initForm() {
    this.registroForm = this.fb.group({
      nit: new FormControl('', [Validators.required]),
      nombreEmpresa: new FormControl('', [Validators.required]),
      nEmpleados: new FormControl('', [Validators.required]),
      sectorEconomico: new FormControl('', [Validators.required]),
      telefono: new FormControl('', [Validators.required]),
      correo: new FormControl('', [Validators.required, Validators.email]),
      direccion: new FormControl('', [Validators.required]),
      municipio: new FormControl('', [Validators.required])
    });
  }

  obtenerRecursos() {
    this.cargando = true;
    this.registroEmpresaService.getObtenerMunicipios().subscribe(
      res => {
        this.municipios = res;
      },
      error => {
        swl({
          title: "",
          text: 'El sistema presenta inconvenientes',
          icon: "warning",
          buttons: [false, true],
        }).then((willDelete) => {
          this.router.navigate(['/inicio'])
        });
      }
    )
    this.registroEmpresaService.getObtenerSectoresEconomicosEmpresa().subscribe(
      res => {
        this.sectoresEconomicos = res;
        this.cargando = false;
      },
      error => {
        swl({
          title: "",
          text: 'El sistema presenta inconvenientes',
          icon: "warning",
          buttons: [false, true],
        }).then((willDelete) => {
          this.router.navigate(['/inicio'])
        });
      }
    )
  }

  creaEmpresa() {
    this.cargando = true;
    if (this.registroForm.valid) {
      let body: Empresa = {
        nit: '',
        nombre: '',
        numeroEmpleados: 0,
        idSectorEconomico: 0,
        telefono: '',
        email: '',
        direccion: '',
        idMunicipio: 0,
      }
      body.nit = this.registroForm.get('nit').value;
      body.nombre = this.registroForm.get('nombreEmpresa').value;
      body.numeroEmpleados = this.registroForm.get('nEmpleados').value;
      body.idSectorEconomico = this.registroForm.get('sectorEconomico').value;
      body.telefono = this.registroForm.get('telefono').value;
      body.email = this.registroForm.get('correo').value;
      body.direccion = this.registroForm.get('direccion').value;
      body.idMunicipio = this.registroForm.get('municipio').value;
      this.registroEmpresaService.getCrearEmpresa(body).subscribe(
        res => {
          let icono = '';
          let mensaje = ''
          if (res.id !== 0) {
            let datosEmpresa: ResponseValidarExisteEmpresa = {
              idEmpresa: res.id,
              nombreEmpresa: body.nombre
            }
            sessionStorage.setItem('datos-empresa', JSON.stringify(datosEmpresa));
            icono = "success";
            mensaje ="La empresa se cre exitosamente."
          }else{
            icono = "warning"
            mensaje = res.mensaje
          }
          swl({
            title: mensaje,
            text: "",
            buttons: [false, true],
            icon: icono
          })
            .then((willDelete) => {
              if (willDelete) {
                this.router.navigate(['/solicitud-empresa']);
              } else {
              }
            });
        },
        error => {
          swl({
            title: "",
            text: 'El sistema presenta inconvenientes',
            icon: "warning",
            buttons: [false, true],
          }).then((willDelete) => {
            this.router.navigate(['/inicio'])
          });
        });
    } else {
      this.cargando = false;
    }
  }

  cancelar() {
    sessionStorage.removeItem('nit');
    sessionStorage.removeItem('datos-empresa');
    this.registroForm.reset();
    this.router.navigate(['/solicitud-empresa']);
  }

}
