import { Component, OnInit, OnDestroy, OnChanges, DoCheck, Output, EventEmitter, Input } from '@angular/core';
import { RegistroEstudianteService } from './registro-estudiante.service';
import { Router } from '@angular/router';
import swl from 'sweetAlert';

@Component({
  selector: 'app-registro-estudiante',
  templateUrl: './registro-estudiante.component.html',
  styleUrls: ['./registro-estudiante.component.scss']
})
export class RegistroEstudianteComponent implements OnInit, DoCheck {

  @Output() select = new EventEmitter<number>();
  @Output() idPrograma = new EventEmitter<number>();
  facultades: any;
  programas: any;
  idFacultad: number;
  requisitos = '';
  idpro: number = 0;
  validacion = false;
  confirmar = false;
  cargando = false;

  constructor(private serviseRegistro: RegistroEstudianteService, private router: Router) {
  }

  ngOnInit() {
    sessionStorage.setItem('cargando', 'true');
    this.getFacultades();
  }

  ngDoCheck(){
    this.cargando = sessionStorage.getItem('cargando') ? true : false;
  }

  requisito(value) {
    for (let index = 0; index < this.programas.length; index++) {
      const element = this.programas[index];
      if (element.id === value) {
        this.requisitos = element.requisitosProgramaPractica
      }

    }
  }

  getFacultades() {
    this.serviseRegistro.getFacultades().subscribe(
      res => {
        sessionStorage.removeItem('cargando');
        this.facultades = res;
      },
      error => {
        sessionStorage.removeItem('cargando');
        swl({
          title: "",
          text: 'El sistema presenta inconvenientes',
          icon: "warning",
          buttons: [false, true],
        }).then((willDelete) => {
          this.router.navigate(['/inicio'])
        });
      }
    );
  }

  getProgramasporFacultad() {
    sessionStorage.setItem('cargando', 'true')
    const id = this.idFacultad;
    this.serviseRegistro.getProgramaPorFacultad(id).subscribe(
      res => {
        this.programas = res;
        sessionStorage.removeItem('cargando');
      },
      error => {
        sessionStorage.removeItem('cargando');
        swl({
          title: "",
          text: 'El sistema presenta inconvenientes',
          icon: "warning",
          buttons: [false, true],
        }).then((willDelete) => {
          this.router.navigate(['/inicio'])
        });
      }
    );
  }

  onSubmit() {
    this.select.emit(2);
    this.idPrograma.emit(this.idpro);
  }

}
