import { NgModule, Component } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { SolicitudEmpresaComponent } from './empresas/solicitud-empresa/solicitud-empresa.component';
import { DetalleSolicitudesEmpresaComponent } from './empresas/detalle-solicitudes-empresa/detalle-solicitudes-empresa.component';
import { ContenedorEstudiantesComponent } from './estudiantes/contenedor-estudiantes/contenedor-estudiantes.component';
import { InicioComponent } from './inicio/inicio.component';
import { RegistroEmpresaComponent } from './empresas/registro-empresa/registro-empresa.component';
import { SolicitudPracticanteComponent } from './empresas/solicitud-practicante/solicitud-practicante.component';

const routes: Routes = [
   { path: '', redirectTo:'/inicio', pathMatch: 'full' },
   { path: 'inicio', component: InicioComponent},
   { path: 'solicitud-empresa', component: SolicitudEmpresaComponent },
   { path: 'solicitar-practicante', component: SolicitudPracticanteComponent },
   { path: 'registro-de-empresa', component: RegistroEmpresaComponent },
   { path: 'detallesolicitudesempresa', component: DetalleSolicitudesEmpresaComponent },
   { path: 'registro-de-estudiante', component: ContenedorEstudiantesComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }

export const routingComponents = [SolicitudEmpresaComponent, DetalleSolicitudesEmpresaComponent];
