import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { ResponseValidarExisteEmpresa } from 'src/utils/models/validar-existe-empresa';
import { environment } from '../../../environments/environment.prod';
import { apis, endpointsEmpresa } from '../../../utils/const';
import { ResponseSolicitudEmpresa, ResponseEnviarSolicitud } from 'src/utils/models/solicitud-empresa';

@Injectable({
  providedIn: 'root'
})
export class SolicitudEmpresaService {

  constructor(private http: HttpClient) { }

  getValidarExisteEmpresa(nit): Observable<ResponseValidarExisteEmpresa> {
    const params = new HttpParams()
    .set('nit', nit);
    return this.http.get<ResponseValidarExisteEmpresa>(
      environment.endpoint +
      apis.empresas +
      endpointsEmpresa.getObtenerDatosEmpresa, {params});
  }

  postEnviarSolicitudEmpresa(body: ResponseSolicitudEmpresa):Observable<ResponseEnviarSolicitud>{
    return this.http.post<ResponseEnviarSolicitud>(
      environment.endpoint +
      apis.empresas +
      endpointsEmpresa.postEnviarSolicitudEmpresa, body
      );
  }
}
