import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Programas } from 'src/utils/models/programas';
import { environment } from 'src/environments/environment.prod';
import { apis, endpointsEmpresa } from 'src/utils/const';
import { TipoVinculacion } from '../../../utils/models/tipo-vinculacion';
import { endpointsMaestros, endpointsEstudiante } from '../../../utils/const';
import { ProgramasDisponibles } from 'src/utils/models/programas-disponibles';
import { CrearSolicitudEmpresaEnEspera } from 'src/utils/models/solicitud-empresa';

@Injectable({
  providedIn: 'root'
})
export class SolicitudPracticanteService {

  constructor(private http: HttpClient) { }

  getProgramas(): Observable<Programas[]> {
    return this.http.get<Programas[]>(
      environment.endpoint +
      apis.maestros +
      endpointsMaestros.getObtenerProgramas
    );
  }
  getTipoVinculacion(): Observable<TipoVinculacion> {
    return this.http.get<TipoVinculacion>(
      environment.endpoint +
      apis.maestros +
      endpointsMaestros.getObtenerTiposVinculacionEmpresa
    );
  }

  getExistenEstudiantesDisponiblesPrograma(idPrograma, idEmpresa): Observable<ProgramasDisponibles> {
    const params = new HttpParams()
      .set('idPrograma', idPrograma)
      .set('idEmpresa', idEmpresa);

    return this.http.get<ProgramasDisponibles>(
      environment.endpoint +
      apis.estudiante +
      endpointsEstudiante.getExistenEstudiantesDisponiblesPrograma, { params }
    );
  }

  postCrearSolicitudEmpresaEnEspera(idPrograma, idEmpresa): Observable<CrearSolicitudEmpresaEnEspera> {
    const body = {
      idEmpresa: idEmpresa,
      idPrograma: idPrograma
    }

    return this.http.post<CrearSolicitudEmpresaEnEspera>(
      environment.endpoint +
      apis.empresas +
      endpointsEmpresa.postCrearSolicitudEmpresaEnEspera, body
    );
  }
}
